.wrap {
  width: 1000px;
  padding: 5%;
  margin: 10% auto 0 auto;
  position: relative;
  display: grid;
  grid-template-columns: 9fr 1fr;
  grid-template-rows: 90% auto;
  grid-template-areas: 'drum-machine side' 'bottom side'; }

#drum-machine {
  display: grid;
  grid-template-columns: 2fr 1fr;
  padding: 2%;
  z-index: 3;
  background-color: #abb2b9; }
  #drum-machine .drum-pad-bank {
    padding: 3%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; }
    #drum-machine .drum-pad-bank .drum-pad {
      border-radius: 10px;
      width: 90%;
      margin-bottom: 10px;
      background-color: #eeeeee;
      -webkit-box-shadow: 3px 4px 5px black;
              box-shadow: 3px 4px 5px black;
      text-align: center;
      line-height: 100px;
      font-weight: bold; }
  #drum-machine .controller-container {
    text-align: center;
    display: grid;
    grid-template-rows: 35% 30% 35%; }
    #drum-machine .controller-container .row {
      position: relative; }
      #drum-machine .controller-container .row.switch-container .switch {
        position: absolute;
        bottom: 15%;
        left: 35%; }
      #drum-machine .controller-container .row.display-container #display {
        margin: 15% auto;
        width: 70%;
        line-height: 30px;
        background-color: white;
        font-family: digitalFont;
        font-weight: bold; }
      #drum-machine .controller-container .row.volume-container .range {
        position: relative;
        top: 15%; }

.bottom {
  grid-area: bottom;
  height: 30px;
  width: 100.3%;
  position: relative;
  top: -1px;
  left: 16.8px;
  z-index: 2;
  -webkit-transform: skew(49deg);
  -moz-transform: skew(49deg);
  -o-transform: skew(49deg);
  border-right: #353a3f 5px solid;
  background-color: #515961; }

.side {
  grid-area: side;
  width: 36px;
  height: 92.8%;
  position: relative;
  top: 14.9px;
  -webkit-transform: skewY(40deg);
  -moz-transform: skewY(40deg);
  -o-transform: skewY(40deg);
  background-color: #515961; }



